import React, { useState, useEffect } from "react";
import { getAllPokemon, getPokemon } from "./services/pokemons";
import Card from "./Card";

function Pokemon() {
  const [pokemonData, setPokemonData] = useState([]);
  const [nextUrl, setNextUrl] = useState("");
  const [prevUrl, setPrevUrl] = useState("");
  const [loading, setLoading] = useState(true);
  const initialUrl = "https://pokeapi.co/api/v2/pokemon";

  useEffect(() => {
    async function fetchData() {
      let response = await getAllPokemon(initialUrl);
      setNextUrl(response.next);
      setPrevUrl(response.previous);
      let pokemon = await loadingPokemon(response.results);
      console.log(pokemon);
      setLoading(false);
    }
    fetchData();
  }, []);

  const next = async () => {
    setLoading(true);
    let data = await getAllPokemon(nextUrl);
    await loadingPokemon(data.results);
    setNextUrl(data.next);
    setPrevUrl(data.previous);
    setLoading(false);
  };

  const prev = async () => {
    if (!prevUrl) return;
    setLoading(true);
    let data = await getAllPokemon(prevUrl);
    await loadingPokemon(data.results);
    setNextUrl(data.next);
    setPrevUrl(data.previous);
    setLoading(false);
  };

  const loadingPokemon = async (data) => {
    let _pokemon = await Promise.all(
      data.map(async (pokemon) => {
        let pokemonRec = await getPokemon(pokemon.url);
        return pokemonRec;
      })
    );

    setPokemonData(_pokemon);
    console.log(_pokemon);
  };

  return (
    <div>
      {loading ? (
        <div className="loader"></div>
      ) : (
        <>
          <div className="btn">
            <button onClick={prev}><i className="fas fa-angle-double-left"></i> Previous</button>
            <button onClick={next}>Next Page <i className="fas fa-angle-double-right"></i></button>
          </div>
          <div className="grid-container">
            {pokemonData.map((pokemon, i) => {
              return <Card key={i} pokemon={pokemon} />;
            })}
          </div>
          <div className="btn">
            <button onClick={prev}><i className="fas fa-angle-double-left"></i> Previous</button>
            <button onClick={next}>Next Page <i className="fas fa-angle-double-right"></i></button>
          </div>
        </>
      )}
    </div>
  );
}

export default Pokemon;
